import React, { Component } from 'react';
import { apiURL, sanitizeHtml } from '../utils';  
import Footer from '../include/Footer';
import Header from '../include/Header';
import { makeStyles, withStyles } from '@material-ui/core/styles'; 
import { CssBaseline, Grid, Container, Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField,Table, TableBody, TableCell, TableHead, TableRow, Snackbar, Switch, InputAdornment } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert'; 
import axios from 'axios';
import { Editor } from '@tinymce/tinymce-react';   
import SearchIcon from '@material-ui/icons/Search'; 
import Helmet from 'react-helmet';


const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({

}))


 class Faq extends Component {
    _isMounted = false;
    constructor(props){
        super(props);
        this.state = {
            drawerOpen: false,
            showDialog: false, 
            faqData: [],
            page: 1,
            per: 10,
            total_pages: null,
            loading: false, 
            showSnackbar: false,
            snackBarMessage: '',
            severityColor: '',
            mode: '',  
            editorVlaue: '',  
            snackBarTimer: '', 
            searchValue:''
        }
        this.handleDrawerOpen       = this.handleDrawerOpen.bind(this);
        this.faqAddDialog          = this.faqAddDialog.bind(this);
        this.faqDialogClose        = this.faqDialogClose.bind(this);
        this.changeHandler          = this.changeHandler.bind(this);
        this.onFormSubmit           = this.onFormSubmit.bind(this);
        this.onEditFormSubmit       = this.onEditFormSubmit.bind(this);
        this.editDialogOpen         = this.editDialogOpen.bind(this);
        this.handleEditorChange     = this.handleEditorChange.bind(this); 
        this.changeStatus           = this.changeStatus.bind(this)
    }

    handleDrawerOpen(drawerState){
        this.setState({
          drawerOpen: drawerState,
        })
    }


    changeHandler = (e, type) => { 
        this.setState({ 
            [e.target.name]: e.target.value,
        }); 
        //console.log(e.target.name +'===='+e.target.value);
    }
    handleImageChange = (e) => {
        this.setState({  
            imagePreview: URL.createObjectURL(e.target.files[0]),
            file:e.target.files[0],
            imageEdit: true
         })
    }

    componentDidMount() { 
        this._isMounted = true; 
        this.faqListData();
        this.scrollListener = window.addEventListener("scroll", e => {
            this.handleScroll(e);
        });
    }

    componentWillUnmount() {
        this._isMounted = false; 
    }
 
  
    faqListData = () => {
        this.setState({ loading: true });
        const { per, page, faqData } = this.state; 
        const headers = { 
            'Content-type': 'application/json'
        }
        const body = {
            "search": document.getElementById('search').value,
           //"search": this.state.searchValue,
        } 
        axios.post(apiURL('faq?per_page='+per+'&page='+page), body, {headers: headers})
        .then(response => {
            
        if(this._isMounted){
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            { 
                this.setState({  
                    faqData: (resultData.total <= 10)?resultData.result:[...this.state.faqData, ...resultData.result],
                    scrolling: false,
                    total_pages: resultData.total_pages,
                });
                this.setState({ loading: false,  scrolling: true, });
            }
            else{
                this.setState({ loading: false,  scrolling: false, }); 
            }
        }
        })
    
        .catch(error => {
            console.log(error)
        }) 
    }

    loadMore = () => { 
        if(this._isMounted){
            this.setState( prevState => ({
                page: prevState.page + 1,
                scrolling: true
            }),
            this.faqListData()
            ); 
        }
      };

      handleScroll = () => {   
        if(this._isMounted){ 
            var lastLi = document.querySelector("Table"); 
            if(lastLi !== null){
                var lastLiOffset = lastLi.offsetTop + lastLi.clientHeight;
                var pageOffset = window.pageYOffset + window.innerHeight;
                //console.log(lastLiOffset+' ===== ' +pageOffset)
                if (pageOffset > lastLiOffset) {
                    if(this.state.scrolling){
                        this.loadMore();
                    }
                } 
            }
        }
      };

    onFormSubmit = (e) => { 
        e.preventDefault();
        //e.stopPropagation();  
        const formData  = new FormData(); 
        formData.append('title', this.state.title); 
        formData.append('description', this.state.editorVlaue);
        const headers = {
            'content-type': 'multipart/form-data',
            'Accept': 'application/json'
        }   
        axios.post(apiURL('faq/add'),formData, {headers: headers})
        .then(response => {
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            {
                this.faqDialogClose();
                this.componentDidMount();
                this.setState({ imageEdit: false, imagePreview: null, file: null  }); 
                this.setState({ 
                    showSnackbar: true, 
                    snackBarMessage:  resultData.message,
                    severityColor: resultData.status === true?'success':'error',
                    snackBarTimer: 3000
                });
            }
            else{
                this.setState({ 
                    showSnackbar: true, 
                    snackBarMessage:  resultData.message,
                    severityColor: 'error',
                    snackBarTimer: 2000
                });
            }
           
        })
        .catch(error => {
            console.log(error)
        }) 
    }

    onEditFormSubmit = (e) => {
         //alert(this.state.name);
         e.preventDefault(); 
         const editFormData  = new FormData();
         editFormData.append('id', this.state.id);
         editFormData.append('title', this.state.title); 
         editFormData.append('description', this.state.editorVlaue); 
         const headers = {
             'content-type': 'multipart/form-data',
             'Accept': 'application/json'
         }
         axios.post(apiURL('faq/edit'),editFormData, {headers: headers})
         .then(response => {
             const resultData = response.data; 
             if(resultData.status === true || resultData.status === 1)
             {
                 this.setState({ imageEdit: false, imagePreview: null, file: null });
                 this.faqDialogClose();
                 this.componentDidMount(); 
                 this.faqListData(); 
                 this.setState({ 
                    showSnackbar: true, 
                    snackBarMessage:  resultData.message, 
                    severityColor: resultData.status === true?'success':'error',
                    snackBarTimer: 3000
                });
             }
            else{
                this.setState({ 
                    showSnackbar: true, 
                    snackBarMessage:  resultData.message,
                    severityColor: 'error',
                    snackBarTimer: 2000
                });
            }
         })
         .catch(error => {
             console.log(error)
         })
    }

    changeStatus = (id, status) => { 
        const headers = { 
            'Content-type': 'application/json'
        }
        axios.get(apiURL('faq/change_status/'+id+'/'+status), {headers: headers})
        .then(response => {
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            { 
                const updatedfaq = this.state.faqData.map(faq=>{
                    if(faq.fldi_id === id){
                        const _faq = {...faq};
                        _faq.flg_status = status==="0"?"1":"0";
                        return _faq;
                    }
                    return  faq;
                });
                this.setState({ faqData: updatedfaq });
            }
        })
        .catch(error => {
            console.log(error)
        })
      }

    faqAddDialog = (e) => { 
        this.setState({ 
            showDialog: true,
            mode: 'add', 
            title: '', 
            editorVlaue: '',  
        });
    }

    editDialogOpen = (e) => { 
        this.setState({ 
            showDialog: true,
            mode: 'edit',
            id: e.fldi_id, 
            title: e.fldv_title, 
            editorVlaue: e.fldt_description,
        });  
    }

    faqDialogClose = () => { 
        this.setState({ showDialog: false });
    }

    snackBarClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
          }
          this.setState({ showSnackbar: false }); 
    }

    handleEditorChange = (content, editor) => { 
        this.setState({
            editorVlaue: content
        });
      }
 
    searchHandler = (e) => {
        this.setState({page: 1, searchValue:e.target.value})
        this.faqListData();
    }
   
    render(){
        const { classes } = this.props; 
        const loadingCSS = {
            height: "30px",
            margin: "10px"
          };
        const loadingTextCSS = { display: this.state.loading ? "block" : "none" };
        return(
            <div>
                <Helmet>
                    <title>Faq</title>
                </Helmet>
                <Header forwardDrawerOpen={this.handleDrawerOpen.bind(this)}  />
                <CssBaseline /> 
                <div style={{ paddingTop: '5rem', paddingLeft: (this.state.drawerOpen?drawerWidth+15:72+15), transition: 'padding 0.5s ease'   }}>
                    <main>    
                        <Container maxWidth="lg" > 
                        <Alert 
                            action={
                                <div>
                                    <TextField type="text" placeholder="...Search"  
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position="start">
                                                    <SearchIcon />
                                                </InputAdornment>
                                            ),
                                            style: {color: 'white',backgroundColor:'rgba(255, 255, 255, 0.29)'}
                                        }} 
                                        size="small"
                                        id="search"
                                        variant="outlined"
                                        style={{fontColor:"#fff"}} 
                                        name="search"
                                        onChange={e => this.searchHandler(e)} 
                                    />
                                    {/*<Searchbar onChange={this.faqListData} />*/}
                                    <Button color="inherit" size="small" onClick={this.faqAddDialog}> Add Faq </Button>  
                                </div>
                            } 
                            variant="filled" severity="success">Faq
                        </Alert>
                            <Grid container spacing={3}  align="center" > 
                                <Grid item xs={12}>  
                                    <Table size="small" stickyHeader aria-label="sticky table">
                                        <TableHead> 
                                            <TableCell>Id</TableCell> 
                                            <TableCell> Title</TableCell> 
                                            <TableCell>Description</TableCell> 
                                            <TableCell>Status</TableCell>
                                            <TableCell>Action</TableCell>
                                            
                                            <TableCell>scrolling</TableCell>
                                        </TableHead>
                                        <TableBody> 
                                            {this.state.faqData.map((arrList, index) => {    
                                             return <TableRow key={arrList.fldi_id}>  
                                                    <TableCell>{index+1}</TableCell> 
                                                    <TableCell>{arrList.fldv_title}</TableCell> 
                                                    <TableCell>{sanitizeHtml(arrList.fldt_description)}</TableCell> 
                                                    <TableCell> <Switch checked={arrList.flg_status==="1"} color="secondary" onChange={() => this.changeStatus(arrList.fldi_id, arrList.flg_status)}/></TableCell>
                                                    <TableCell><Button variant="outlined" color="primary" onClick={() => this.editDialogOpen(arrList)}>Edit</Button></TableCell>
                                                    
                                                    <TableCell>{this.state.scrolling}</TableCell> 
                                                </TableRow>
                                            })} 
                                        </TableBody>
                                    </Table> 
                              
                                    <div ref={loadingRef => (this.loadingRef = loadingRef)} style={loadingCSS} >
                                        <span style={loadingTextCSS}>Loading...</span>
                                    </div>
                                    
                                </Grid>
                           </Grid> 
                           <Footer />
                        </Container>
                    </main>
                </div> 

                <Dialog open={this.state.showDialog} fullWidth={true} maxWidth={'md'}>
                    <form  id="courses"  onSubmit={this.state.mode === 'edit'?(e) => this.onEditFormSubmit(e):(e) => this.onFormSubmit(e) }  >
                        <DialogTitle id="form-dialog-title">{(this.state.mode === 'add')?'Add':'Edit'} faq</DialogTitle>
                        <DialogContent style={{overflowY: 'hidden'}}>  
                            <Grid container spacing={3}>
                                <Grid item xs={12} sm={12}>   
                                    <TextField fullWidth type="text" label="Title" variant="outlined" name="title"  size="small" onChange={ e => this.changeHandler(e)} value={this.state.title} />
                                </Grid> 
                                 
                                
                                <Grid item xs={12} sm={12}>
                                <Editor
                                    initialValue={this.state.editorVlaue}
                                    init={{
                                    height: 188,
                                    menubar: true,
                                    plugins: [
                                        'advlist autolink lists link image charmap print preview anchor',
                                        'searchreplace visualblocks code fullscreen',
                                        'insertdatetime media table paste code help wordcount'
                                    ],
                                    toolbar:
                                        'undo redo | formatselect | bold italic backcolor | \
                                        alignleft aligncenter alignright alignjustify | \
                                        bullist numlist outdent indent | removeformat | help'
                                    }}
                                    onEditorChange={(e) => this.handleEditorChange(e)}
                                />
                                </Grid>  
                             </Grid>
                        </DialogContent>
                        <DialogActions>
                            <Button variant="contained" onClick={() => this.faqDialogClose()} color="secondary"> Cancel </Button>
                            <Button type="submit" variant="contained" color="primary"> Submit </Button>
                        </DialogActions>
                    </form>
                </Dialog>

                <Snackbar open={this.state.showSnackbar === true} autoHideDuration={this.state.snackBarTimer} onClose={this.snackBarClose}>
                    <Alert onClose={this.snackBarClose} severity={this.state.severityColor} elevation={6} variant="filled">
                    { this.state.snackBarMessage }
                    </Alert>
                </Snackbar>
            </div>
        );
    }
 }
  
 export default (withStyles)(useStyles)(Faq);