import React, { Component } from 'react';
import { apiURL, change_date_format, get_changed_time, sanitizeHtml } from '../utils';  
import Footer from '../include/Footer';
import Header from '../include/Header';
import { makeStyles, withStyles } from '@material-ui/core/styles'; 
import { CssBaseline, Grid, Container, Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField,Table, TableBody, TableCell, TableHead, TableRow, Snackbar, Switch, Select, MenuItem, InputLabel, FormControl, InputAdornment } from '@material-ui/core';
import Alert from '@material-ui/lab/Alert'; 
import axios from 'axios'; 
import SearchIcon from '@material-ui/icons/Search';
import { Editor } from '@tinymce/tinymce-react';  
import DateFnsUtils from '@date-io/date-fns'; 
import { KeyboardDatePicker, KeyboardTimePicker, MuiPickersUtilsProvider } from "@material-ui/pickers"; 
import Helmet from 'react-helmet';

const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
    textField: {
        height:'20px'
    },
}))
 class Training_schedule extends Component {
    _isMounted = false;
    constructor(props){
        super(props);
        this.state = {
            drawerOpen: false,
            showDialog: false,
            selecteDate:  new Date(),
            changeDate: '',
            dialogTitle: '',
            file: null,
            imagePreview: null,
            imageEdit: false, 
            page: 1,
            per:10,
            total_pages: null,
            loading: false,
            showSnackbar: false,
            snackBarMessage: '',
            severityColor: '',
            mode: '',  
            editorVlaue: '',
            selecteDate:  new Date(),
            changeDate: '',
            selectFromTime:  new Date(),
            selectToTime:  new Date(),
            speakerData: [],
            trainingScheduleData: [],
            speaker: ''
        }
        this.handleDrawerOpen       = this.handleDrawerOpen.bind(this);
        this.addDialog              = this.addDialog.bind(this);
        this.editDialog             = this.editDialog.bind(this);
        this.closeDialog            = this.closeDialog.bind(this);
        this.handleDateChange       = this.handleDateChange.bind(this);
        this.handleFromTimeChange   = this.handleFromTimeChange.bind(this);
        this.handleToTimeChange     = this.handleToTimeChange.bind(this);
        this.changeHandler          = this.changeHandler.bind(this);
        this.handleImageChange      = this.handleImageChange.bind(this);
        this.handleEditorChange     = this.handleEditorChange.bind(this);
        this.onFormSubmit           = this.onFormSubmit.bind(this);
        this.trainingSchudeleListData  = this.trainingSchudeleListData.bind(this);
    }

    componentDidMount() { 
        this._isMounted = true;  
        this.speakerListData(); 
        this.trainingSchudeleListData();
        this.scrollListener = window.addEventListener("scroll", e => {
            this.handleScroll(e);
        });
        
    }

    componentWillUnmount() {
        this._isMounted = false; 
    }

    trainingSchudeleListData = () => {
        this.setState({ loading: true });
        const { per, page, trainingScheduleData } = this.state; 
        const headers = { 
            'Content-type': 'application/json'
        }
        const body = {
            "search": document.getElementById('search').value
        }
        axios.post(apiURL('training_schedule?per_page='+per+'&page='+page),body, {headers: headers})
        .then(response => {
            
        if(this._isMounted){
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            { 
                this.setState({  
                    trainingScheduleData: (resultData.total < 10)?resultData.result:[...this.state.trainingScheduleData, ...resultData.result],
                    scrolling: false,
                    total_pages: resultData.total_pages,
                });
                this.setState({ loading: false, scrolling: true });
            }
            else{
                this.setState({ loading: false, scrolling: false }); 
            }
        }
        })
        .catch(error => {
            console.log(error)
        })
    }

    loadMore = () => { 
        if(this._isMounted){
            this.setState(
            prevState => ({
                page: prevState.page + 1,
                scrolling: true
            }),
            this.trainingSchudeleListData()
            ); 
        }
      }

      handleScroll = () => {   
        if(this._isMounted){ 
            var lastLi = document.querySelector("Table"); 
            if(lastLi !== null){
                var lastLiOffset = lastLi.offsetTop + lastLi.clientHeight;
                var pageOffset = window.pageYOffset + window.innerHeight;
                if (pageOffset > lastLiOffset) {
                    if(this.state.scrolling){
                        this.loadMore();
                    }
                } 
            }
        }
      }


    speakerListData = () => { 
        const headers = { 
            'Content-type': 'application/json'
        }
        axios.get(apiURL('training_schedule/speaker'), {headers: headers})
        .then(response => {
            
        if(this._isMounted){
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            { 
                this.setState({  
                    speakerData: resultData.result, 
                }); 
            } 
        }
        })
        .catch(error => {
            console.log(error)
        })
    }

    handleDrawerOpen(drawerState){
        this.setState({
          drawerOpen: drawerState,
        })
    }

    changeHandler = (e, type) => { 
        this.setState({ 
            [e.target.name]: e.target.value,
        }); 
    }

    handleImageChange = (e) => {
        this.setState({  
            imagePreview: e !== null? URL.createObjectURL(e.target.files[0]):'',
            file:e !== null?e.target.files[0]:'',
            imageEdit: true
         })
    }

    addDialog = (e) => { 
        this.setState({ 
            mode:'add',
            showDialog: true,
            dialogTitle: 'Add Training Schedule'
         });
    }

    editDialog = (e) => { 
        this.setState({ 
            mode:'edit',
            showDialog: true, 
            dialogTitle: 'Edit Training Schedule',
            id: e.fldi_id,
            title: e.fldv_title,
            speaker: e.fldi_speaker_id,
            selecteDate: new Date(e.fldv_select_date),
            selectFromTime: new Date(e.fldv_from_time),
            selectToTime: new Date(e.fldv_to_time),
            editorVlaue: e.fldv_description,
            image: e.fldv_image_url,
        });
    }

    closeDialog = () => { 
        this.setState({ 
            showDialog: false,
            title: '',
            speaker: '',
            selecteDate: new Date(),
            selectFromTime: new Date(),
            selectToTime: new Date(),
            editorVlaue: '',
            image:'',
            file: ''
         });
    }

    handleDateChange = (date) => { 
        console.log(date) 
        this.setState({
            selecteDate: new Date(date), 
            changeDate: change_date_format(date)
        });
    }

    
    handleFromTimeChange = (date) => { 
        console.log(date) 
        this.setState({
            selectFromTime: new Date(date), 
            changeDate: change_date_format(date)
        });
    }

    
    handleToTimeChange = (date) => { 
        console.log(date) 
        this.setState({
            selectToTime: new Date(date), 
            changeDate: change_date_format(date)
        });
    }

    handleEditorChange = (content, editor) => { 
        this.setState({
            editorVlaue: content
        });
      }

    onFormSubmit = (e) => { 
        e.preventDefault();
        const formData  = new FormData();
        formData.append('title', this.state.title);
        formData.append('speaker', this.state.speaker);
        formData.append('select_date', this.state.selecteDate);  
        formData.append('change_date', change_date_format(this.state.selecteDate)+' '+get_changed_time(this.state.selecteDate, false)); 
        formData.append('from_time', this.state.selectFromTime);
        //formData.append('changed_from_time', get_changed_time(this.state.selectFromTime));
        formData.append('to_time', this.state.selectToTime);
        //formData.append('changed_to_time', get_changed_time(this.state.selectToTime));
        formData.append('description', this.state.editorVlaue);
        formData.append('file', this.state.file);
        const headers = { 
            'content-type': 'multipart/form-data',
            'Accept': 'application/json'
        }
        axios.post(apiURL('training_schedule/add'),formData, {headers: headers})
        .then(response => {
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            {
                this.closeDialog();
                this.componentDidMount();
                this.setState({ imageEdit: false, imagePreview: null, file: null  }); 
                this.setState({ 
                    showSnackbar: true, 
                    snackBarMessage:  resultData.message,
                    severityColor: resultData.status === true?'success':'error',
                    snackBarTimer: 3000
                });
             }
             else{
                 this.setState({ 
                    showSnackbar: true, 
                     snackBarMessage:  resultData.message,
                     severityColor: 'error',
                     snackBarTimer: 2000
                 });
            }
           
        })
        .catch(error => {
            console.log(error)
        }) 
    }

    onEditFormSubmit = (e) => { 
        e.preventDefault(); 
        const editFormData  = new FormData();
        editFormData.append('id', this.state.id);
        editFormData.append('title', this.state.title);
        editFormData.append('speaker', this.state.speaker);
        editFormData.append('select_date', this.state.selecteDate);  
        editFormData.append('change_date', change_date_format(this.state.selecteDate)+' '+get_changed_time(this.state.selecteDate, false)); 
        editFormData.append('from_time', this.state.selectFromTime);
        //editFormData.append('changed_from_time', get_changed_time(this.state.selectFromTime));
        editFormData.append('to_time', this.state.selectToTime);
        //editFormData.append('changed_to_time', get_changed_time(this.state.selectToTime));
        editFormData.append('description', this.state.editorVlaue);
        if(this.state.file !== null){
            editFormData.append('file', this.state.file);
        }
        editFormData.append('exist_file', this.state.image);
        editFormData.append('image_change', this.state.imageEdit);
        const headers = {
            'content-type': 'multipart/form-data',
            'Accept': 'application/json'
        }
        axios.post(apiURL('training_schedule/edit'),editFormData, {headers: headers})
        .then(response => {
            const resultData = response.data; 
            if(resultData.status === true || resultData.status === 1)
            {
                this.setState({ imageEdit: false, imagePreview: null, file: null });
                this.closeDialog();
                this.componentDidMount();  
                this.setState({ 
                   showSnackbar: true, 
                   snackBarMessage:  resultData.message, 
                   severityColor: resultData.status === true?'success':'error',
                   snackBarTimer: 3000
                });
             }
             else{
                 this.setState({ 
                     showSnackbar: true, 
                     snackBarMessage:  resultData.message,
                     severityColor: 'error',
                     snackBarTimer: 2000
                 });
            }
           
        })
        .catch(error => {
            console.log(error)
        })
   }

   changeStatus = (id, status) => { 
       const headers = { 
           'Content-type': 'application/json'
       }
       axios.get(apiURL('training_schedule/change_status/'+id+'/'+status), {headers: headers})
       .then(response => {
           const resultData = response.data; 
           if(resultData.status === true || resultData.status === 1)
           { 
               const updatedTrainingSchedule = this.state.trainingScheduleData.map(ts=>{
                   if(ts.fldi_id === id){
                       const _ts = {...ts};
                       _ts.flg_status = status==="0"?"1":"0";
                       return _ts;
                   }
                   return ts;
               });
               this.setState({ trainingScheduleData: updatedTrainingSchedule });
           }
       })
       .catch(error => {
           console.log(error)
       })
     }

     searchHandler = (e) => {
        this.setState({page: 1, searchValue:e.target.value})
        this.trainingSchudeleListData();
    }

    snackBarClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
          }
          this.setState({ showSnackbar: false }); 
    }

    render(){
        const { classes } = this.props; 
        const loadingCSS = {
            height: "30px",
            margin: "10px"
          };
        const loadingTextCSS = { display: this.state.loading ? "block" : "none" };
        return(
            <React.Fragment>
                <Helmet>
                    <title>Training Schedule</title>
                </Helmet>
                <Header forwardDrawerOpen={this.handleDrawerOpen.bind(this)}  />
                <CssBaseline /> 
                <div style={{ paddingTop: '5rem', paddingLeft: (this.state.drawerOpen?drawerWidth+15:72+15), transition: 'padding 0.5s ease'   }}>
                    <main>    
                        <Container maxWidth="lg" > 
                        <Alert 
                            action={
                                <div>
                                    <TextField type="text" placeholder="...Search"  
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position="start">
                                                    <SearchIcon />
                                                </InputAdornment>
                                            ),
                                            style: {color: 'white',backgroundColor:'rgba(255, 255, 255, 0.29)'}
                                        }} 
                                        size="small"
                                        id="search"
                                        variant="outlined"
                                        style={{fontColor:"#fff"}} 
                                        name="search"
                                        onChange={e => this.searchHandler(e)} 
                                    /> 
                                    <Button color="inherit" size="small" onClick={(e) => this.addDialog(e)}> Add Training Schedule </Button>  
                                </div>
                            } 
                            variant="filled" severity="success">Training Schedule
                        </Alert>
                            <Grid container spacing={3}  align="center" > 
                                <Grid item xs={12}>  
                                <Table size="small" stickyHeader aria-label="sticky table">
                                        <TableHead> 
                                            <TableRow>
                                                <TableCell>Id</TableCell> 
                                                <TableCell> Title</TableCell>
                                                <TableCell>Description</TableCell>
                                                <TableCell>Speaker</TableCell>
                                                <TableCell>Schedule Date</TableCell>
                                                <TableCell>From Time</TableCell>
                                                <TableCell>To Time</TableCell>
                                                <TableCell>Image</TableCell>
                                                <TableCell>Status</TableCell>
                                                <TableCell>Action</TableCell>
                                            </TableRow>
                                        </TableHead>
                                        <TableBody> 
                                            {this.state.trainingScheduleData.map((arrList, index) => {    
                                             return <TableRow key={arrList.fldi_id}>  
                                                    <TableCell>{index+1}</TableCell> 
                                                    <TableCell>{arrList.fldv_title}</TableCell>
                                                    <TableCell>{sanitizeHtml(arrList.fldv_description)}</TableCell>
                                                    <TableCell>{arrList.speaker_name}</TableCell>
                                                    <TableCell>{arrList.fldv_changed_date}</TableCell>
                                                    <TableCell>{get_changed_time(arrList.fldv_from_time, true)}</TableCell>
                                                    <TableCell>{get_changed_time(arrList.fldv_to_time, true)}</TableCell>
                                                    <TableCell><img style={{width:"40px"}} src={arrList.fldv_image_url} alt={arrList.fldv_name} /></TableCell>
                                                    <TableCell> <Switch checked={arrList.flg_status==="1"} color="secondary" onChange={() => this.changeStatus(arrList.fldi_id, arrList.flg_status)}/></TableCell>
                                                    <TableCell><Button variant="outlined" color="primary" onClick={() => this.editDialog(arrList)}>Edit</Button></TableCell>
                                                </TableRow>
                                            })} 
                                        </TableBody>
                                    </Table> 
                              
                                    <div ref={loadingRef => (this.loadingRef = loadingRef)} style={loadingCSS} >
                                        <span style={loadingTextCSS}>Loading...</span>
                                    </div>
                                   
                                </Grid>
                           </Grid>
                           <Footer />
                        </Container>
                    </main>
                </div> 

                <Dialog open={this.state.showDialog} fullWidth={true} maxWidth={'md'}>
                    <form  id="courses"  onSubmit={this.state.mode === 'edit'?(e) => this.onEditFormSubmit(e):(e) => this.onFormSubmit(e) } >
                        <DialogTitle id="form-dialog-title">{this.state.dialogTitle}</DialogTitle>
                        <DialogContent  style={{overflowY: 'hidden'}}>  
                            <Grid container spacing={3}>
                                <Grid item xs={12} sm={6}>   
                                    <TextField fullWidth type="text" size="small" label="Title" variant="outlined" name="title" onChange={ e => this.changeHandler(e)} value={this.state.title} required />
                                </Grid> 
                                <Grid item xs={12} sm={6}>
                                    <FormControl variant="outlined" size="small" style={{width:'100%'}}>
                                        <InputLabel>Speaker</InputLabel>
                                        <Select name="speaker" value={this.state.speaker} onChange={ e => this.changeHandler(e)} label="Speaker" required>
                                            <MenuItem value=""> <em>None</em> </MenuItem>
                                            {this.state.speakerData.map((arrQualification, index) => {
                                                return <MenuItem key={arrQualification.fldi_id} value={arrQualification.fldi_id}>{arrQualification.fldv_name}</MenuItem>
                                            })}
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <FormControl size="small" style={{width:'100%'}} > 
                                        <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                        <KeyboardDatePicker inputVariant="outlined" label="Choose Date" value={this.state.selecteDate}
                                            onChange={(date) => this.handleDateChange(date)}  name="date" format="dd/MM/yyyy"
                                            showTodayButton minDate={new Date(this.state.selecteDate)} autoOk variant="inline"
                                        />
                                        </MuiPickersUtilsProvider> 
                                    </FormControl>
                                </Grid> 
                                <Grid item xs={12} sm={6}>   
                                    <MuiPickersUtilsProvider utils={DateFnsUtils} >
                                    <Grid container spacing={3} >
                                    <Grid item xs={6} sm={6}>
                                        <KeyboardTimePicker ampm={true} variant="inline" inputVariant="outlined" label="Start Time"
                                            value={this.state.selectFromTime} onChange={(date) => this.handleFromTimeChange(date)} 
                                        />
                                        </Grid>
                                        <Grid item xs={6} sm={6}>
                                        <KeyboardTimePicker ampm={true} variant="inline" inputVariant="outlined" label="End Time"
                                            value={this.state.selectToTime} onChange={(date) => this.handleToTimeChange(date)}
                                        />
                                        </Grid>
                                        </Grid>
                                    </MuiPickersUtilsProvider> 
                                </Grid>  
                                <Grid item xs={12} sm={12}>
                                    <Editor
                                        initialValue={this.state.editorVlaue}
                                        init={{
                                        height: 188,
                                        menubar: true,
                                        plugins: [
                                            'advlist autolink lists link image charmap print preview anchor',
                                            'searchreplace visualblocks code fullscreen',
                                            'insertdatetime media table paste code help wordcount'
                                        ],
                                        toolbar:
                                            'undo redo | formatselect | bold italic backcolor | \
                                            alignleft aligncenter alignright alignjustify | \
                                            bullist numlist outdent indent | removeformat | help'
                                        }}
                                        onEditorChange={(e) => this.handleEditorChange(e)}
                                    /> 
                                </Grid> 
                                <Grid item xs={12} sm={6}>
                                    <Button variant="contained" color="primary" component="label">Select Main Image
                                        <input type="file" style={{ display: "none" }} onChange={this.handleImageChange} />
                                    </Button>
                                    
                                    {this.state.imageEdit === true?<img style={{width:'40px'}} src={this.state.imagePreview}/>:<img style={{width:'40px'}} src={this.state.image}/>}
                                </Grid>  
                             </Grid>
                        </DialogContent>
                        <DialogActions>
                            <Button variant="contained" color="secondary"  onClick={(e) => this.closeDialog(e)} >  Cancel  </Button>
                            <Button type="submit" variant="contained" color="primary"> Submit </Button>
                        </DialogActions>
                    </form>
                </Dialog>
                
                <Snackbar open={this.state.showSnackbar === true} autoHideDuration={this.state.snackBarTimer} onClose={this.snackBarClose}>
                    <Alert onClose={this.snackBarClose} severity={this.state.severityColor} elevation={6} variant="filled">
                    { this.state.snackBarMessage }
                    </Alert>
                </Snackbar>
            </React.Fragment>
        );
    }
 }
  
 export default (withStyles)(useStyles)(Training_schedule);