import React, { Component } from 'react';
import { IconButton, Badge, Popover, Typography } from '@material-ui/core';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { makeStyles, withStyles } from '@material-ui/core/styles'; 
import axios from 'axios'; 
import { apiURL } from '../utils'; 

const useStyles = makeStyles((theme) => ({
    typography: {
        padding: theme.spacing(6),
      },
}));

class Notification extends Component {
    constructor(props){
        super(props); 
        this.state = {
            popoverOpen: null,
            notificationCount: 0,
            notificationData: [],
        }
        this.notificationClick    = this.notificationClick.bind(this);
        this.updateNotification   = this.updateNotification.bind(this);
    } 

     componentDidMount() { 
        const headers = { 
            'Content-type': 'application/json'
        }
        axios.get(apiURL('notification'), {headers: headers})
        .then(response => {
            const resultData = response.data;
            if(resultData.status === true || resultData.status === 1)
            { 
                this.setState({ 
                    notificationCount: resultData.result.length,
                    notificationData: resultData.result
                 });
            }
        })
        .catch(error => {
            console.log(error)
        })
    }

    updateNotification = (id) => { 
        const headers = { 
            'Content-type': 'application/json'
        }
        axios.get(apiURL('notification/update/'+id), {headers: headers})
        .then(response => {
            const resultData = response.data;
            console.log(resultData);
            if(resultData.status === true || resultData.status === 1)
            { 
                const count =  this.state.notificationData.length; 
                this.setState({ 
                    notificationCount: (count - 1), 
                 });
                 this.componentDidMount();
            }
        })
        .catch(error => {
            console.log(error)
        })
    }

    notificationClick = (event) => { 
        this.setState({ 
            popoverOpen: event.currentTarget
        }); 
    }

    notificationClose = (event) => {
        this.setState({
            popoverOpen: null
        });
    }

    render(){
        const open = Boolean(this.state.popoverOpen);
        const id = open ? 'simple-popover' : undefined;
        //const { classes } = this.props; 
        const classes = this.props.classes 
        return (
            <div>
                <IconButton color="inherit" aria-describedby={id} onClick={(e) => this.notificationClick(e)} >   
                    <Badge color="secondary" aria-describedby="notification" badgeContent={this.state.notificationCount} >
                        <NotificationsIcon />
                    </Badge>
                </IconButton>
                <Popover 
                    open={open} 
                    onClose={(e) => this.notificationClose(e) } 
                    anchorEl={this.state.popoverOpen}  
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center', 
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                    }}
                    >
                    {this.state.notificationData.map((arrList, index) => {    
                     return (<Typography key={arrList.fldi_id} style={{padding:'1px 10px'}} onClick={() => this.updateNotification(arrList.fldi_id)} >{arrList.fldv_key}</Typography>);
                     
                    })}
                </Popover>
            </div>
        )
    }
}

export default (withStyles)(useStyles)(Notification);