import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router, Switch, Route, BrowserRouter} from 'react-router-dom';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';
import Dashboard from './dashboard/Dashboard';
import Signin from './sign-in/Signin';
import Article from './article/Article';
import Video from './video/Video';
import News from './news/News';
import Training_schedule from './training_schedule/Training_schedule';
import Speaker from './speaker/Speaker';
import Subscribe from './subscribe/Subscribe';
import Get_in_touch from './get_in_touch/Get_in_touch';
import Category from './category/Category';
import Blog from './blog/Blog';
import Faq from './faq/Faq';
import Webinar from './webinar/Webinar'; 
import Team from './team/Team'; 

import NetworkDetector from './utils/NetworkDetector';

class App extends Component {
  /*constructor(props){
    super(props)
  }*/
  render(){
    return (
      <div className="App">
	  <NetworkDetector />

    <BrowserRouter basename={"/sign-in"}></BrowserRouter>
        <Router basename={"/sign-in"}>
          <div>
            <Switch> 
              <PublicRoute restricted={true} component={Signin} path="/" exact />
              <PublicRoute restricted={true} component={Signin} path="/sign-in" exact />
              <PrivateRoute component={Dashboard} path="/dashboard" exact />
              <PrivateRoute component={Article} path="/article" exact />
              <PrivateRoute component={Video} path="/video" exact />
              <PrivateRoute component={News} path="/announcement-news" exact />
              <PrivateRoute component={Training_schedule} path="/training-schedule" exact />
              <PrivateRoute component={Speaker} path="/speaker" exact />
              <PrivateRoute component={Subscribe} path="/subscriber" exact />
              <PrivateRoute component={Get_in_touch} path="/get-in-touch" exact />
              <PrivateRoute component={Category} path="/category" exact />
              <PrivateRoute component={Blog} path="/blog" exact />
              <PrivateRoute component={Faq} path="/faq" exact />
              <PrivateRoute component={Webinar} path="/webinar" exact />
              <PrivateRoute component={Team} path="/team" exact />
              
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
