import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DashboardIcon from '@material-ui/icons/Dashboard'; 
import PeopleIcon from '@material-ui/icons/People';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import AnnouncementIcon from '@material-ui/icons/Announcement'; 
import ScheduleIcon from '@material-ui/icons/Schedule';
import SpeakerIcon from '@material-ui/icons/Speaker';
import UnsubscribeIcon from '@material-ui/icons/Unsubscribe';
import TrackChangesIcon from '@material-ui/icons/TrackChanges';
import CategoryIcon from '@material-ui/icons/Category';
import BookIcon from '@material-ui/icons/Book';
import LiveHelpIcon from '@material-ui/icons/LiveHelp';
import LanguageIcon from '@material-ui/icons/Language';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import AssignmentIcon from '@material-ui/icons/Assignment';  
import { Link } from 'react-router-dom';
import ScatterPlotIcon from '@material-ui/icons/ScatterPlot';
import PersonIcon from '@material-ui/icons/Person';
import GroupIcon from '@material-ui/icons/Group';

const useStyles = makeStyles((theme) =>
  createStyles({
    nested: {
      paddingLeft: theme.spacing(4),
    },
  }),
);

export default function Sidenav() {
  
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [userOpen, setUserOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const handleUserClick = () => {
    setUserOpen(!userOpen);
  };

  return (
  <List> 
    <ListItem button>
      <ListItemIcon>
        <Link to='/dashboard'><DashboardIcon /></Link>
      </ListItemIcon>
      <Link to='/dashboard'><ListItemText primary="Dashboard" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to='/article'><AssignmentIcon /></Link>
      </ListItemIcon>
      <Link to='/article'><ListItemText primary="Article" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to='/video'><VideoLibraryIcon /></Link> 
      </ListItemIcon>
      <Link to='/video'><ListItemText primary="Video" /></Link>
    </ListItem>
    <ListItem button>
      <ListItemIcon>
      <Link to='/announcement-news'><AnnouncementIcon /></Link>
      </ListItemIcon>
      <Link to='/announcement-news'><ListItemText primary="Announcement News" /></Link>
    </ListItem> 
    <ListItem button>
      <ListItemIcon>
      <Link to='/training-schedule'><ScheduleIcon /></Link>
      </ListItemIcon>
      <Link to='/training-schedule'><ListItemText primary="Training Schedule" /></Link>
    </ListItem>
    <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <ScatterPlotIcon />
        </ListItemIcon>
        <ListItemText primary="Master" />
        {open ? <ExpandLess /> : <ExpandMore />}
    </ListItem>
    <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
            <ListItem button className={classes.nested}>
              <ListItemIcon>
              <Link to='/speaker'><SpeakerIcon /></Link>
              </ListItemIcon>
              <Link to='/speaker'><ListItemText primary="Speaker" /></Link>
            </ListItem>
            <ListItem button className={classes.nested}>
            <ListItemIcon>
              <Link to='/category'><CategoryIcon /></Link>
              </ListItemIcon>
              <Link to='/category'><ListItemText primary="Category" /></Link>
            </ListItem>
        </List>
      </Collapse>
      <ListItem button onClick={handleUserClick}>
        <ListItemIcon>
          <PersonIcon />
        </ListItemIcon>
        <ListItemText primary="User" />
        {userOpen ? <ExpandLess /> : <ExpandMore />}
    </ListItem>
    
    <Collapse in={userOpen} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button className={classes.nested}>
            <ListItemIcon>
            <Link to='/subscriber'><UnsubscribeIcon /></Link>
            </ListItemIcon>
            <Link to='/subscriber'><ListItemText primary="Subscriber" /></Link>
          </ListItem> 
          <ListItem button className={classes.nested}>
            <ListItemIcon>
            <Link to='/get-in-touch'><TrackChangesIcon /></Link>
            </ListItemIcon>
            <Link to='/get-in-touch'><ListItemText primary="Get In Touch" /></Link>
          </ListItem>
        </List>
    </Collapse>
    <ListItem button>
    <ListItemIcon>
      <Link to='/blog'><BookIcon /></Link>
      </ListItemIcon>
      <Link to='/blog'><ListItemText primary="Blog" /></Link>
    </ListItem>
    
    <ListItem button>
    <ListItemIcon>
      <Link to='/team'><GroupIcon /></Link>
      </ListItemIcon>
      <Link to='/team'><ListItemText primary="Team" /></Link>
    </ListItem>
    <ListItem button>
    <ListItemIcon>
      <Link to='/faq'><LiveHelpIcon /></Link>
      </ListItemIcon>
      <Link to='/faq'><ListItemText primary="FAQ" /></Link>
    </ListItem>
    <ListItem button>
    <ListItemIcon>
      <Link to='/webinar'><LanguageIcon /></Link>
      </ListItemIcon>
      <Link to='/webinar'><ListItemText primary="Webinar" /></Link>
    </ListItem>

  </List>
  
);
}
