import React, { Component } from 'react';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';

class NetworkDetector extends Component {
   
	constructor(props){
		super(props)
		this.state = {
			isDisconnected: false,
			vertical: 'top',
			horizontal: 'center',
		}
	}

    componentDidMount() {
      this.handleConnectionChange();
      //this.interval = setInterval(() => this.handleConnectionChange(), 4000);
      window.addEventListener('online', this.handleConnectionChange);
      window.addEventListener('offline', this.handleConnectionChange);
    }

    componentWillUnmount() {
      window.removeEventListener('online', this.handleConnectionChange);
      window.removeEventListener('offline', this.handleConnectionChange);
    }


    handleConnectionChange = () => {
      const condition = navigator.onLine ? 'online' : 'offline';
      if (condition === 'online') {
        const webPing = setInterval(
          () => {
            fetch('https://beelive.in', { mode: 'no-cors', })
            .then(() => {
              this.setState({ isDisconnected: false }, () => {
                return clearInterval(webPing)
              });
            }).catch(() => this.setState({ isDisconnected: true }), )
          }, 2000);
        return;
      }

      return this.setState({ isDisconnected: true });
    }

    render() {
      const { isDisconnected, vertical, horizontal } = this.state;
      return (
        <div>
          { isDisconnected && (<div className="internet-error">
              <Snackbar open={true} anchorOrigin={{ vertical, horizontal }} key={vertical + horizontal}>
              <Alert severity="error">
                internet connection lost!
              </Alert>
              </Snackbar>
            </div>)
          }
          {/*<ComposedComponent {...this.props} />*/}
        </div>
      );
    }
  }
  
  export default NetworkDetector;